@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 800px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: "NanumSquare", sans-serif;
}

.d-block {
  height: 400px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.shopinfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 250px;
  background: rgb(41, 41, 41);
  color: rgb(226, 226, 226);
}

.first {
  text-shadow: 2px 2px 2px rgb(48, 48, 48);
}

.info_title {
  font-size: 1.2rem;
}

.shopinfo_list {
  width: 70%;
  display: flex;
  justify-content: space-evenly;
  text-align: left;
  font-size: 0.7rem;
}

.shoppage {
  margin-top: 50px;
  margin-bottom: 50px;
}

.goods {
  cursor: pointer;
}

.goods img {
  border-radius: 10px;
}

.col-md-3 {
  margin-bottom: 20px;
}

.more {
  text-align: right;
  cursor: pointer;
}

.icons {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
}

.icons_best {
  padding: 1px;
  font-size: 0.8em;
  border-radius: 10px;
  background-color: tomato;
  color: white;
  width: 35px;
}

.icons_new {
  padding: 1px;
  font-size: 0.8em;
  border-radius: 10px;
  background-color: orange;
  width: 30px;
}

.my_alert{background:#f1e7e7;padding:20px;border-radius:10px}.my_alert p{margin-bottom:0}.title{padding:20px}.row{margin-top:20px;margin-bottom:20px}.wow-enter{opacity:0}.wow-enter-active{opacity:1;color:red;transition:all 500ms}.qna{display:flex;flex-direction:column}.qna_table{border-top:2px solid;border-bottom:2px solid;border-color:#a5a1a1}.qna_table thead{background-color:#e7d8d8}.qna_button{width:5rem;align-self:flex-end;margin-bottom:10px}.answer{background-color:#e6dbdb}.answer .h5{font-weight:600}

